
          @import "~@/assets/style/_mixins.scss";
          @import "~@/assets/style/_variables.scss";
          @import "~@/assets/style/_mediaQueries.scss";
          @import "~@/assets/style/_svg.scss";
          @import "~@/assets/style/_common.scss";
          @import "~@/assets/style/_reset.scss";
        

.hello {
  max-width: $max-width;
  margin: 0 auto;
  letter-spacing: 0.03rem;
  color: #fff;
  label {
    display: none;
  }
  &__guide {
    padding-top: 2.4rem;
    margin-bottom: 6rem;
    font-size: 1.6rem;
    line-height: 1.75;
    letter-spacing: 0.03rem;
  }
  &__guide-text {
    display: block;
  }
  &__ask {
    font-size: 2.8rem;
    font-weight: bold;
    margin-bottom: 4rem;
  }
}
//애니메이션 관리
.hello {
  &__guide-text {
    @for $i from 1 through 2 {
      &:nth-child(#{$i}) {
        @include animation(fadeShow, 450ms, 1, #{$i * 200}ms);
      }
    }
  }
  &__ask {
    @include animation(fadeShow, 750ms, 1, 900ms);
  }
  .main-input {
    @include animation(fadeShow, 850ms, 1, 1100ms);
  }
}
